import React from "react";
import mainData from "../../content/Main.yml";
import { Link } from "gatsby";

const ExpertiseSection = () => {
  const data = mainData.expertise;

  return (
    <article data-sal="slide-up" data-sal-delay="300" data-sal-easing="ease">
      <h2>{data.title}</h2>
      <p>{data.description}</p>
      <p>Main technologies: {data.main}</p>

      { data.areas.map(area => (
        <>
          <h3>{area.name}</h3>
          <p>{area.description}</p>
        </>
      ))}

      <Link className="nav-link" to="/projects">{data.next}</Link>
    </article>
  );
};

export default ExpertiseSection;